.nav-container{
	width: 100%;
	height: 56px;
	background-color: black;
	display: flex;
	justify-content: space-between;
	align-items: center;

}
.nav-logo{
	width: 100px;
	margin-left: 10%;
	margin-top: 30px;
}
.nav-button{
	width: 160px;
	height: 43px;
	background-color: #fd1d1d;
	border: 1px solid black;
	border-radius: 25px;
	color: white;
	font-size: 1.1rem;
	
	outline: none;
}
.nav-button:hover{
	background-color: #ff8585;

}

@media (max-width: 1200px) {
.nav-logo{
	width: 80px;
	margin-left: 10%;
	margin-top: 30px;
}
.nav-container{
   height: 50px;
}
.nav-button{
	width: 130px;
	height: 36px;
	border-radius: 25px;
	font-size: 0.8rem;
}
}



@media (max-width: 600px) {
.nav-logo{
	width: 60px;
	margin-left: 10%;
	margin-top: 25px;
}
.nav-container{
   height: 46px;
}
.nav-button{
	width: 100px;
	height: 30px;
	border-radius: 25px;
	font-size: 0.6rem;
}
}