.Main-title{
	font-family: 'Cinzel Decorative', cursive;
	font-weight: 700;
	font-size: 3rem
}
.Main-example{
	font-family: 'Cinzel Decorative', cursive;
	font-weight: 400;
	font-size: 2.5rem
}
.Main-cover{
	width: 60%;
}

@media (max-width: 1200px) {
.Main-cover{
	width: 80%;
}
.Main-title{
	
	font-size: 1.7rem
}
.Main-example{
	font-size: 1.3rem
}
}



@media (max-width: 600px) {
.Main-cover{
	width: 100%;
}
.Main-title{
	font-size: 1.1rem
}
.Main-example{
	font-size: 1rem
}
}