.Card-main-container{
	width: 200px;
	height: 420px;
	border: 0.5px solid black;
	margin: 15px;
	border-radius: 15px;
	position: relative;
	-webkit-box-shadow: 0px 0px 16px -1px rgba(0,0,0,0.36);
    -moz-box-shadow: 0px 0px 16px -1px rgba(0,0,0,0.36);
    box-shadow: 0px 0px 16px -1px rgba(0,0,0,0.36);
}
.photo{
	width: 100%;
	border-top-right-radius: 15px;
	border-top-left-radius: 15px;
}
.Card-title{
	font-family: 'Cinzel Decorative', cursive;
	font-weight: 700;
}
.Card-rating{
		font-family: 'Cinzel Decorative', cursive;
	   font-weight: 400;
    
}
.Card-button{
  position: absolute;
  top:5px; 
  left:5px;
  margin: auto;
  width: 90px;
  height: 30px;
  border-radius: 22px;
  border: 1.5px solid #1adb7c;
  background-color: black;
  color: white;
  z-index: 100;
  outline: none;
}
.Card-button:hover{
background-color: #1adb7c;

}