.InputBox-container{
	width: 100%;
	height: 56px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
	

}
.InputBox-input{
	width: 270px;
	height: 41px;
    border-top-left-radius: 11px;
	border-bottom-left-radius: 11px;
	border:1px solid black;
	background-color: #f3f3f3;
	font-size: 1.1rem;
	outline: none;

}
.InputBox-button{
	width: 200px;
	height: 45px;
	background-color: #2640f0;
	border:none;
	border-top-right-radius: 11px;
	border-bottom-right-radius: 11px;
	outline: none;
	color: white;
	font-size: 1.1rem;
}
.InputBox-button:hover{
	background-color: #7687f8;
}

@media (max-width: 1200px) {
.InputBox-input{
	width: 220px;
	height: 35px;
}
.InputBox-button{
	width: 140px;
	height: 39px;
	font-size: 1rem;
}
}

@media (max-width: 600px) {
.InputBox-input{
	width: 170px;
	height: 30px;
}
.InputBox-button{
	width: 120px;
	height: 35px;
	font-size: 1rem;
}
}