.MovieInfo-top{
	display: flex;
	flex-direction: column;
}
.MovieInfo-container{
	width: 100%;
	display: flex; flex-direction: column;
	align-items: center;

	
}
.MovieInfo-small{
	display: flex;
	flex-direction: row;
	width: 80%;
	
 }
 .MovieInfo-text{
 	display: flex;
 	flex-direction: column;
 	align-items: flex-start;
 	text-align: left;
 	margin-left: 5%;

 }

.MovieInfo-noimage{
	width: 500px;
	height: 750px;
	background-color: #e3e3e3
}
.MovieInfo-title{
	 font-family: 'Cinzel Decorative', cursive;
  font-weight: 700;
  font-size: 5rem;
 
}
.MovieInfo-goback{
	margin-top: 30px;
	margin-left: 15px;
	width: 190px;
	height: 37px;
	background-color: #2640f0;
	outline: none;
	color: white;
	font-size: 1.1rem;
	border-radius: 25px;
	border: 0.5px solid white;
}
.MovieInfo-goback:hover{
	background-color: #6f9ef0;
}


@media (max-width: 1200px) {

}

@media (max-width: 600px) {
.MovieInfo-small{
	display: flex;
	align-items: center;
	flex-direction: column;
	width: 100%;
	
 }
 .MovieInfo-mainphoto{
 	width: auto;
 }
 .MovieInfo-title{
  font-size: 1.5rem;
 
}
.MovieInfo-goback{
	margin-top: 30px;
	margin-left: 5px;
	width: 160px;
	height: 30px;
	font-size: 0.8rem;
}
.MovieInfo-noimage{
	width: auto;
	height: auto;
	background-color: #e3e3e3
}
 .MovieInfo-text{
 	display: flex;
 	flex-direction: column;
 	align-items: center;
 	text-align: center;
 	margin-left: 0;

 }
}